<template>
  <div>
    <div class="task-div-one">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>首页</el-breadcrumb-item>
        <el-breadcrumb-item>任务管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="task-div-two">
      <el-row>
        <search-bar
          class="task-search-bar"
          ref="searchBar"
          @onSearch="searchResult"
        ></search-bar>
      </el-row>
      <div>
        <el-table
          :data="
            tasks.slice((currentPage - 1) * pagesize, currentPage * pagesize)
          "
          border
          :height="735 + 'px'"
          style="width: 100%"
        >
          <el-table-column prop="id" label="ID" width="150"> </el-table-column>
          <el-table-column prop="taskId" label="task_id" width="150">
          </el-table-column>
          <el-table-column prop="stage" label="task阶段" width="200">
          </el-table-column>
          <el-table-column
            prop="status"
            label="任务状态"
            width="200"
            :formatter="forStatus"
          >
          </el-table-column>
          <el-table-column
            prop="intendedVehicle"
            label="task分配车辆"
            width="200"
          >
          </el-table-column>
          <el-table-column prop="takeLocation" label="确定取货库位" width="200">
          </el-table-column>
          <el-table-column
            prop="deliveryLocation"
            label="确定放货库位"
            width="200"
          >
          </el-table-column>
          <el-table-column prop="project" label="工程类型" width="200">
          </el-table-column>
          <el-table-column prop="createTime" label="创建时间" width="200">
          </el-table-column>
          <el-table-column prop="updateTime" label="更新时间" width="200">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="120">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="show(scope.row)"
                >修改</el-button
              >
              <el-button
                type="text"
                size="small"
                @click="deleteTasks(scope.row)"
                >删除</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <div>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[10, 15, 20]"
            :page-size="10"
            layout="total, sizes, prev, pager, next, jumper"
            :total="sizes"
          >
          </el-pagination>
        </div>
      </div>
    </div>
    <div>
      <el-dialog title="修改信息" :visible.sync="dialogEditFormVisible">
        <el-form :v-model="form">
          <el-form-item label="task_id">
            <el-input v-model="form.taskId"></el-input>
          </el-form-item>
          <el-form-item label="task阶段">
            <el-input v-model="form.stage"></el-input>
          </el-form-item>
          <el-form-item label="任务状态">
            <el-input v-model="form.status"></el-input>
          </el-form-item>
          <el-form-item label="task分配车辆">
            <el-input v-model="form.intendedVehicle"></el-input>
          </el-form-item>
          <el-form-item label="确定取货库位">
            <el-input v-model="form.takeLocation"></el-input>
          </el-form-item>
          <el-form-item label="确定放货库位">
            <el-input v-model="form.deliveryLocation"></el-input>
          </el-form-item>
          <el-form-item label="工程类型">
            <el-input v-model="form.project"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="clear">取 消</el-button>
          <el-button type="primary" @click="editTasks">修改</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import dayjs from "dayjs";
import SearchBar from "../components/SearchBar.vue";
export default {
  name: "Task",
  components: {
    SearchBar,
  },
  data() {
    return {
      dialogEditFormVisible: false,
      pagesize: 10,
      currentPage: 1,
      tasks: [],
      sizes: 0,
      isSear: 0,
      form: {
        id: "",
        taskId: "",
        stage: "",
        status: "",
        intendedVehicle: "",
        takeLocation: "",
        deliveryLocation: "",
        project: "",
        createTime: "",
      },
    };
  },
  mounted: function () {
    this.loadTasks();
  },
  methods: {
    handleSizeChange(val) {
      this.pagesize = val;
      if (this.isSear === 0) {
        this.loadTasks();
      }
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      if (this.isSear === 0) {
        this.loadTasks();
      }
    },
    loadTasks() {
      this.tasks = [];
      for (let i = 0; i < this.currentPage; i++) {
        this.$axios
          .get("/web/task/list?page=" + i + "&size=" + this.pagesize + "", {})
          .then((resp) => {
            this.tasks = this.tasks.concat(resp.data.data.content);
            this.sizes = resp.data.data.totalElements;
          });
      }
    },
    deleteTasks(row) {
      this.$confirm("确认删除？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$axios
            .delete("/api/task/delete?id=" + row.id, {})
            .then((resp) => {
              this.loadTasks();
              this.$emit("deleteTasks", row);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });
    },
    clear() {
      this.form = {
        id: "",
        taskId: "",
        stage: "",
        status: "",
        intendedVehicle: "",
        takeLocation: "",
        deliveryLocation: "",
        project: "",
      };
      this.dialogEditFormVisible = false;
    },
    show(row) {
      this.dialogEditFormVisible = true;
      this.form = {
        id: row.id,
        taskId: row.taskId,
        stage: row.stage,
        status: row.status,
        intendedVehicle: row.intendedVehicle,
        takeLocation: row.takeLocation,
        deliveryLocation: row.deliveryLocation,
        project: row.project,
        createTime: row.createTime,
      };
    },
    editTasks() {
      var date1 = dayjs(new Date().getTime() - 24 * 60 * 60 * 1000).format(
        "YYYY-MM-DD HH:mm:ss"
      );
      this.$axios
        .post("/api/task/edit", {
          id: this.form.id,
          taskId: this.form.taskId,
          stage: this.form.stage,
          status: this.form.status,
          intendedVehicle: this.form.intendedVehicle,
          takeLocation: this.form.takeLocation,
          deliveryLocation: this.form.deliveryLocation,
          project: this.form.project,
          createTime: this.form.createTime,
          updateTime: date1,
        })
        .then((resp) => {
          if (resp && resp.data.code === 200) {
            this.$emit("editTasks", row);
          }
          this.loadTasks();
          this.clear();
        });
    },
    searchResult() {
      if (this.$refs.searchBar.keywords != "") {
        this.$axios
          .get("/api/task/find?taskId=" + this.$refs.searchBar.keywords, {})
          .then((resp) => {
            this.tasks = resp.data.data;
            this.sizes = this.tasks.length;
          });
      } else {
        this.isSear = 0;
        this.loadTasks();
      }
    },
    forStatus(row, index) {
      if (row.status == 10) {
        row.status = "进行中";
      }
      if (row.status == 20) {
        row.status = "已完成";
      }
      return row.status;
    },
  },
};
</script>

<style>
.task-div-one {
  margin: 20px 0px;
}
.task-div-two {
  overflow: hidden;
  margin: 5px 10px;
}
.task-search-bar {
  float: left;
}
.task-add {
  float: right;
}
</style>


